'use client';

import { motion, AnimatePresence } from 'framer-motion';
import Link from 'next/link';
import { useState, useEffect } from 'react';
import { usePathname } from 'next/navigation';
import Image from 'next/image';
import { FiMenu, FiX, FiChevronRight } from 'react-icons/fi';

const menuVariants = {
  hidden: { x: '100%' },
  visible: { 
    x: 0,
    transition: { 
      type: 'spring',
      stiffness: 200,
      damping: 30
    }
  },
  exit: { 
    x: '100%',
    transition: { 
      ease: 'easeInOut',
      duration: 0.2 
    } 
  }
};

const itemVariants = {
  hidden: { opacity: 0, x: 50 },
  visible: (i: number) => ({
    opacity: 1,
    x: 0,
    transition: { delay: i * 0.1 }
  }),
  exit: { opacity: 0, x: 50 }
};

const hoverVariants = {
  hover: { 
    y: -3,
    transition: { 
      type: 'spring',
      stiffness: 400,
      damping: 10
    }
  }
};

function cleanPath(path: string) {
    return path === "/" ? "/" : path.replace(/\/+$/, "");
}

export default function Navbar() {
    const [isOpen, setIsOpen] = useState(false);
    const [scrolled, setScrolled] = useState(false);
    const pathname = usePathname();

    useEffect(() => {
        const handleScroll = () => setScrolled(window.scrollY > 50);
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
    }, []);

    const links = [
        { href: '/', label: 'Home' },
        { href: '/about', label: 'About' },
        { href: '/services', label: 'Services' },
        { href: '/gallery', label: 'Gallery' },
        { href: '/blog', label: 'Blog' },
        { href: '/contact', label: 'Contact' },
    ];

    return (
        <header className={`fixed w-full z-50 transition-all duration-300 ${scrolled ? 'bg-white shadow-md py-2' : 'bg-transparent py-4'}`}>
            <nav className="container mx-auto px-4 sm:px-6 lg:px-8">
                <div className="flex justify-between items-center">
                    {/* Logo */}
                    <motion.div
                        initial={{ opacity: 0, y: -20 }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{ duration: 0.5 }}
                        className="flex items-center"
                    >
                        <Link href="/" className="flex items-center gap-3">
                            <Image
                                src="/images/logo.png"
                                alt="logo"
                                width={scrolled ? 48 : 56}
                                height={scrolled ? 48 : 56}
                                className="transition-all duration-300"
                            />
                            <span className={`text-2xl font-extrabold text-[#cd9826] transition-all duration-300 ${scrolled ? 'text-xl' : 'text-2xl'}`} 
                                    style={{ fontFamily: 'Poppins, sans-serif' }}>
                                Ceed Golden Farm
                            </span>
                        </Link>
                    </motion.div>

                    {/* Desktop Navigation */}
                    <div className="hidden lg:flex items-center space-x-1">
                        {links.map((link) => {
                            const isActive = cleanPath(pathname) === cleanPath(link.href);
                            return (
                                <motion.div
                                    key={link.href}
                                    whileHover="hover"
                                    variants={hoverVariants}
                                    className="relative"
                                >
                                    <Link
                                        href={link.href}
                                        className={`px-5 py-2 text-lg font-medium transition-all rounded-lg ${
                                            isActive 
                                                ? 'text-white bg-gradient-to-r from-[#cd9826] to-[#cd9826] shadow-lg' 
                                                : `${scrolled ? 'text-gray-700 hover:text-[#cd9826]' : 'text-white hover:text-[#cd9826]'}`
                                        }`}
                                        style={{ fontFamily: 'Inter, sans-serif' }}
                                    >
                                        {link.label}
                                        {isActive && (
                                            <motion.span 
                                                className="absolute inset-x-1 -bottom-1 h-0.5 rounded-full"
                                                layoutId="activeIndicator"
                                                transition={{ type: 'spring', stiffness: 300, damping: 30 }}
                                            />
                                        )}
                                    </Link>
                                </motion.div>
                            );
                        })}
                    </div>

                    {/* Mobile Menu Button */}
                    <div className="lg:hidden flex items-center">
                        <motion.button
                            whileHover={{ scale: 1.05 }}
                            whileTap={{ scale: 0.95 }}
                            onClick={() => setIsOpen(true)}
                            className={`p-2 rounded-lg ${scrolled ? 'text-[#cd9826] hover:bg-gray-100' : 'text-white hover:bg-white/20'}`}
                        >
                            <FiMenu size={28} />
                        </motion.button>
                    </div>
                </div>
            </nav>

            {/* Mobile Menu Overlay */}
            <AnimatePresence>
                {isOpen && (
                    <>
                        <motion.div
                            initial={{ opacity: 0 }}
                            animate={{ opacity: 1 }}
                            exit={{ opacity: 0 }}
                            className="fixed inset-0 bg-black/50 z-50"
                            onClick={() => setIsOpen(false)}
                        />

                        <motion.div
                            variants={menuVariants}
                            initial="hidden"
                            animate="visible"
                            exit="exit"
                            className="fixed top-0 right-0 h-full w-80 max-w-full bg-white z-50 shadow-2xl"
                        >
                            <div className="p-6 h-full flex flex-col">
                                <div className="flex justify-between items-center mb-8">
                                    <div className="flex items-center gap-3">
                                        <Image
                                            src="/images/logo.png"
                                            alt="logo"
                                            width={48}
                                            height={48}
                                        />
                                        <span className="text-xl font-extrabold text-[#cd9826]" 
                                              style={{ fontFamily: 'Poppins, sans-serif' }}>
                                            Ceed Golden Farm
                                        </span>
                                    </div>
                                    <motion.button
                                        whileHover={{ scale: 1.1 }}
                                        whileTap={{ scale: 0.9 }}
                                        onClick={() => setIsOpen(false)}
                                        className="text-gray-600 p-2"
                                    >
                                        <FiX size={28} />
                                    </motion.button>
                                </div>

                                <nav className="flex-1 space-y-4">
                                    {links.map((link, index) => {
                                        const isActive = cleanPath(pathname) === cleanPath(link.href);
                                        return (
                                            <motion.div
                                                key={link.href}
                                                custom={index}
                                                variants={itemVariants}
                                                initial="hidden"
                                                animate="visible"
                                                exit="exit"
                                            >
                                                <Link
                                                    href={link.href}
                                                    className={`flex items-center justify-between p-4 rounded-xl text-lg font-medium ${
                                                        isActive 
                                                            ? 'bg-[#cd9826]/10 text-[#cd9826]'
                                                            : 'text-gray-600 hover:bg-gray-50'
                                                    }`}
                                                    onClick={() => setIsOpen(false)}
                                                >
                                                    <span>{link.label}</span>
                                                    <div className="flex items-center">
                                                        {isActive && (
                                                            <motion.div 
                                                                className="w-2 h-2 bg-[#cd9826] rounded-full mr-3"
                                                                initial={{ scale: 0 }}
                                                                animate={{ scale: 1 }}
                                                            />
                                                        )}
                                                        <FiChevronRight className="text-gray-400" />
                                                    </div>
                                                </Link>
                                            </motion.div>
                                        );
                                    })}
                                </nav>

                                <motion.div
                                    initial={{ opacity: 0 }}
                                    animate={{ opacity: 1 }}
                                    transition={{ delay: 0.4 }}
                                    className="mt-8 pt-6 border-t border-gray-100 text-center text-gray-500"
                                >
                                    <p>© {new Date().getFullYear()} Ceed Golden Farm</p>
                                </motion.div>
                            </div>
                        </motion.div>
                    </>
                )}
            </AnimatePresence>
        </header>
    );
}

